@font-face {
    font-family: 'raleway-heavy';
    src: url("/fonts/raleway-heavy-webfont.ttf");
}

@font-face {
    font-family: 'merriweather-bold';
    src: url("/fonts/merriweather-bold-webfont.ttf");
}

@font-face {
    font-family: 'merriweather-light';
    src: url("/fonts/merriweather-light-webfont.ttf");
}

h3, h4{
    font-family: 'merriweather-bold', sans-serif !important;
}

p, article{
    font-family: 'merriweather-light', sans-serif !important;
}

.navbar, li{
    font-family: 'raleway-heavy', sans-serif;
    font-size: 15px;
}

html{
    scroll-behavior: smooth;
}

body{
    min-width: 300px;
}

.perfil{
    display: flex;
    flex-direction: column;
    min-height: 47rem;
    max-height: 77rem;
    margin-top: 66px;
}

@media screen and (max-width: 1051px){
    .perfil{
        margin-bottom: 40px;
    }
}

#blog{
    min-height: 45rem;
    max-height: 104rem;
}

.title{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    font-family: 'merriweather-bold', sans-serif !important;
}

#habilidades{
    background-image: url("/t2/assets/features-bg.jpg");
    background-size: cover;
    text-align: center;
    display: flex;
    flex-direction: column; 
    justify-content: space-around;
    align-items: center;
    min-height: 20rem;
    max-height: 55rem;
}

#trabalhos{
    background-image: url("/t2/assets/intro-bg.jpg");
    background-size: cover;
    min-height: 45rem;
    max-height: 107rem;
    text-align: center;
}

#infos{
    min-height: 30rem;
    max-height: 107rem;
    text-align: center;
}

#contato{
    background: url("/t2/assets/back-contato.jpg");
    background-size: cover;
    min-height: 30rem;
    max-height: 100rem;
}

.p{
    color: #fff;
}

#redes-sociais{
    display: flex;
    flex-direction: row;
    list-style-type: none;
}
.navbar-transparent{
    background-color: transparent !important;
}
.title{
    font-family: sans-serif;
    font-size: 40px;
    margin-top: 15px;
}

.title.black:after{
    content: " ";
    display: block;
    border: 2.5px solid;
    width: 100px;
    margin: 10px;
}

.title.white:after{
    content: " ";
    display: block;
    border: 2.5px solid;
    width: 100px;
    margin: 10px;
}

.white{
    color: white;
}

.black{
    color: black;
}
.foto{
    margin-top: 5px;
    height: 50vh;
    width: 50vh;
    border-radius: 50%;
}

.center{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 25px;
}

.lista{
    margin-top: 30px;
}

#introducao{
    margin-top: 15px;
    text-align: center;
}
#botao{
    text-align: center;
    height: 60px;
    width: 240px;
    background: transparent;
    border: 1px solid black;
    border-radius: 50px;
    -webkit-transition: .5s;
    transition: .5s;
}

#botao:hover{
    cursor: pointer;
    background: black;
    color: white;
}

#botao-white{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80px;
    width: 250px;
    background: transparent;
    border: 1px solid white;
    border-radius: 50px;
    -webkit-transition: .5s;
    transition: .5s;
}

#botao-white:hover{
    cursor: pointer;
    background: white;
    color: black;
}

.link{
    font-size: 1rem;
    color: transparent;
}

.link:hover{
    text-decoration: none;
}

.link:focus{
    outline-color: transparent;
    color: transparent;
}
#flex{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.skill{
    padding: 3px 10px;
    background-color: rgba(0,0,0,.4);
    color: #fff;
    margin: 4px;
    cursor: pointer;
    -webkit-transition: all .4s!important;
    transition: all .4s!important;
    text-transform: uppercase;
    min-width: 60px;
    font-size: 10pt;
}

.skill:hover{
    background-color: #fff;
    color: #000;
}
.center{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.flex{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-width: 375px;   
    max-height: 650px;    
    justify-content: center;
    align-items: center;
    background: rgba(80, 80, 80, .4);
    padding: 30px;
    border-radius: 10px;
    margin-top: 10px;
}

#img{
    height: 200px;
    width: 320px;
    margin-bottom: 10px;
    border-radius: 5px;
}

@media screen and (max-width: 1051px){
    .flex-item{
        width: 150px;
    }
}

.flex-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    grid-column-gap: 5px;
    width: 100%;
}

#editor{
    background-image: url("/t2/assets/text-editor.jpg");
    background-size: cover;
}

#its{
    background-image: url("/t2/assets/its-one.jpg");
    background-size: cover;
}

#casamento{
    background-image: url("/t2/assets/casamento-trabalho.jpg");
    background-size: cover;
}

.flex-item{
    position: relative;
    box-shadow: 2px 2px 7px black;
    width: 400px;
    height: 30rem;
    margin: 10px;
    -webkit-transition: .5s;
    transition: .5s;
}
.sob{
    display: flex;
    justify-content: space-between;
    opacity: 0;
    text-align: justify;
    color: white;
    font-size: 20px;
    background-color: rgba(80,80,80,.8); 
    width: 100%;
    height: 100%;
    -webkit-transition: .5s;
    transition: .5s;
}

.sob:hover{
    opacity: 1;
}

.repositorio{
    position: absolute;
    text-align: center;
    font-size: 17px;
    bottom: 0;
    color: white;
    background-color: black;
    width: 100%;
    height: 50px;
}

.repositorio:hover{
    color: white;
    text-decoration: none;
    cursor: pointer;
}
.container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 50px;
}

.item{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-width: 375px;
    max-height: 650px;
    padding: 20px;
}

.art{
    text-align: justify;
    text-indent: 30px;
}
.formulario{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.flex-itemm{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 65% !important;
}
.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 1rem;
    max-height: 15rem;
}

.feet{
    color: white;
    margin-bottom: .8px;
}
